<template>
   <div class="border-white p-3" :class="borderType">
      <div class="w-full flex flex-col gap-y-1 justify-start"
      v-for="(room,rindex) in rooms" :key="rindex">
         <div class="flex items-center bg-white text-blue px-2 mt-2">
            <span class="w-2/4 md:w-1/3 text-left" v-if="(room.length > 0)"><b class="md:inline block">Habitación: </b>
            {{ (room[0].comb.adults == 0) ? '' : ((room[0].comb.adults > 1) ? room[0].comb.adults + ' adultos' : '1 adulto') }}
            {{ (room[0].comb.children == 0) ? '' : ((room[0].comb.children > 1) ? room[0].comb.children + ' niños' : '1 niño') }}
            </span>
            <span class="w-1/3 text-center md:block hidden"><b>Precio por noche</b></span>
            <span class="w-2/4 md:w-1/3 text-center"><b>Total de noches: </b>{{ (room.length > 0) ? room[0].num_night : '' }}</span>
         </div>
         <div class="flex items-center px-2"
         v-for="(r,i) in room" :key="i">
            <span class="w-2/4 md:w-1/3 text-left">
               <input type="radio" :name="'hotel_room' + rindex" :value="r.room_id"
               @click="selectedRooms({rindex, index: i})">
               {{ r.room }}               
            </span>
            <div class="w-1/3 flex gap-x-2 justify-center md:block hidden">
               <span class="text-orange">
                  <b class="text-white">{{ (r.comb.adults == 0) ? '' : ((r.comb.adults > 1) ? r.comb.adults + ' adultos' : '1 adulto') }}</b> 
                  ${{ r.prices.price_per_adults }}
               </span>
               <span v-if="(r.comb.children > 0)" class="text-orange">
                  <b class="text-white">{{ (r.comb.children == 0) ? '' : ((r.comb.children > 1) ? r.comb.children + ' niños' : '1 niño') }}</b> 
                  ${{ r.prices.price_per_children }}
               </span>
            </div>
            <span class="w-2/4 md:w-1/3 text-center text-orange text-xl">
               <b>${{ r.prices.total_price }}</b>
            </span>
         </div>
      </div>      
   </div>
   
</template>
<script>
import Stars from '@/components/searcher/Stars'

export default {
   name: 'HotelTablePrice',   
   props: {
      rooms: [],
      index: {
         typeof: Number
      },
      borderType: {
         default: 'border-l-2 border-r-2 border-b-2 border-dashed'
      }
   },
   data() {
      return {
         rooms_info: []      
      }
   },
   components: {
      Stars
   }, 
   watch: {
      rooms() {
         if(this.rooms[0].length > 0) {
            for(let el of this.rooms) {            
               const comb = {
                  comb: el[0].comb,
                  rooms: []
               }
               for(let r of el) {
                  comb.rooms.push({
                     id: r.room_id,
                     name: r.room,
                     price: r.prices.total_price,
                     active: false
                  })
               }
               this.rooms_info.push(comb)
            }
         }
      }
   },
   methods: {
      selectedRooms({rindex, index}) {           
         let rooms = [] 
         if(this.rooms_info) {
            this.rooms_info[rindex].rooms.map(e => e.active = false)
            this.rooms_info[rindex].rooms[index].active = true
                     
            // Envio el listado de las habiationces selecciondas por combinacion                 
            // console.log(this.rooms_info)   
            for(let el of this.rooms_info){
               let room_active = el.rooms.filter(r => r.active)          
               
               if(room_active[0]) {
                  room_active = room_active[0]
                  room_active.comb = el.comb    
                  rooms = [...rooms, room_active]
               }
            }
            // console.log('rooms', rooms)
         }
         this.$emit('updRoomRequest', rooms)
      }
   }
}
</script>
