<template>
   <article class="flex flex-col w-9/12">      
      <div class="flex flex-col md:flex-row gap-x-3 md:gap-y-0 gap-y-3" v-if="(is_search)">
         <!-- THUMB PRODUCT -->
         <figure class="md:w-5/12 border-8 border-white"
         :class="{'md:mb-3':(combinations.length > 0)}">
            <img :src="item.image" alt="">
         </figure>            
         <!-- INFO -->
         <div class="md:w-8/12 flex flex-col gap-3 border-t-2 border-l-2 border-r-2 border-dashed border-white p-2"
         :class="{'border-b-2':(combinations.length == 0)}">
            <!-- Info 7 Price From -->
            <div class="w-full flex gap-3">
               <button type="button" @click="goProduct" class="flex flex-col gap-3 pl-2">
                  <h3 class="uppercase text-left text-base font-DIN-Bold md:text-2xl">{{ item.alias }}</h3>
                  <!-- Stars -->
                  <stars :star_init="item.stars"></stars>
                  <small class="text-left py-2">Desde <b class="text-orange text-xlfont-DIN-Bold md:text-2xl">
                     ${{ item.price_from }}</b> {{ (item.per_pax) ? '[por persona / la noche]' : '[por habiación]' }}
                  </small>   
               </button>
               <!-- Segment -->
               <figure class="w-2/12 ml-auto md:block hidden">
                  <img :src="item.segment.img" alt="">
               </figure>                  
            </div>  
            <small class="text-red bg-pink-300 px-10 hidden">Debe marcar al menos una habitación</small>      
            <div class="flex flex-col md:flex-row gap-y-3 mt-auto mx-3 mb-3"
            :class="{'justify-around':(combinations.length > 0)}">
               <button class="uppercase font-DIN-Bold border border-orange text-orange rounded-75 px-8 py-1
               hover:shadow-h-hover-blue-md hover:bg-orange hover:text-blue mt-auto"
               @click="goProduct(item.keyword)">ver mas</button>                  
               <button class="uppercase font-DIN-Bold border border-white text-white rounded-75 px-8 py-1
               hover:shadow-h-hover-blue-md hover:bg-white hover:text-blue mt-auto"
               v-if="(combinations.length > 0)"
               @click="goProduct(item.keyword)">Enviar solicitud</button>                  
            </div>                                     
            
         </div>
      </div>
      <div v-if="(combinations.length > 0)">          
         <hotel-table-price :rooms="combinations"></hotel-table-price>
      </div>        
   </article>
</template>
<script>
import Stars from '@/components/searcher/Stars'
import HotelTablePrice from '@/components/HotelTablePrice'

export default {
   name: 'ProductListTemplate',   
   props: {
      item: [],
      index: {
         typeof: Number
      },
      not_found: {default: false},
      params: {}
   },
   components: {
      Stars,
      HotelTablePrice
   },
   data() {
      return {
         results: []         
      }
   },   
   computed: {
      combinations() {
         const comb_list = []
         // parseo las habitaciones por combinaciones de cada hotel   
         if(this.params.hasOwnProperty('comb')) {
            this.params.comb.map((e,index) => {               
               if(comb_list.length-1 < index) {
                  comb_list.push([])
               }                
               for(let r of this.item.rooms) {
                  r.combination.map(c => {                       
                     if(c) {
                        if(c[0].adults_quantity == e.adults &&
                           c[0].children_quantity == e.children &&
                           c[0].infants_quantity == e.infants) {
                              // console.log(c.lenght)
                           comb_list[index].push({
                              room_id: r.room_id,
                              room: r.name,
                              prices: r.total_prices[index],
                              num_night: c.length,
                              comb: e
                           })
                        }
                     }
                  })
               }
            })
         }
         return comb_list
      },
      is_search() {    
         if(this.not_found) {                  
            return (this.combinations.length > 0)
         }
         return true
      }
   },
   methods: {
      goProduct(keyword = 'iberostar-laguna-azul') {
         this.$router.push({
            name: 'HotelDetail', 
            params: {
               poleKey: this.params.poleKey,  // pais al que pertenece el polo
               productKey: this.params.productKey,
               keyword
            }
         })
      }
   }
}
</script>
